*{
    margin:0;
    padding:0;
}

p{
    margin:0;
    padding: 0;
}
/* Navigation CSS */
.nav-main{
    width:100%;
    background-color: rgb(169, 255, 255);
    padding: 1cm 0.5cm;
}

.nav-child{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.nav-col-2{
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 0.8cm;
}

/*Calendar CSS */

.calendar-main{
    width: 100%;
}
.calendar-child{
    display: flex;
    flex-direction: column;
    margin:0.8cm 1cm;
    border-radius: 15px;
}
.icon{
    width:0.8cm;
}

.icon-active{
    width:fit-content;
    background-color: rgb(255, 153, 180);
    padding:0.2cm;
    border-radius: 15px 15px 0px 0px;
    margin:0;
}

.icon-inactive{
    width:fit-content;
    background-color: rgb(255, 214, 225);
    padding:0.2cm;
    border-radius: 15px 15px 0px 0px;
    margin:0;
}

.calendar-row-1{
    display: flex;
    flex-direction: row;
    column-gap: 0cm;
}

.calendar-row-2{
    background-color: rgb(255, 153, 180);
    border-radius: 0px 15px 15px 15px;
    padding:0.5cm 1cm;
}

.calendar-tile{
    width:2cm;
    max-width: 2cm;
    height:1.5cm;
    max-height: 2cm;
    padding: 0.2cm;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: end;
    border-radius: 5px;
    background-color: rgb(169, 242, 255);
}

.calendar-parent{
    border-radius: 10px;
    padding: 0.8cm;
    display: flex;
    flex-direction: row;
    gap: 0.3cm;
  
}